.input-wrapper {
    background-color: white;
    width: 35%;
    border-radius:10px;
    height: 2rem;
    padding: 0 15px;
    box-shadow: 0px 0px 8px #ddd;
    display: flex;
    align-items: center;
}

.input {
    background-color: transparent;
    border: none;
    height:100%;
    width:100%;
    margin-left:5px;
}

.input:focus {
    outline: none;
}

.search-icon {
    color: royalblue;
}